<template>
  <div id="selfInfo" v-loading="loading" element-loading-background="#f8f8f8">
    <el-row>
      <h2>头像</h2>
      <el-divider></el-divider>
      <el-image
        style="width: 300px; height: 300px; border-radius: 50%; margin: 10px 0"
        :src="userForm.usrAvatar"
        :preview-src-list="[userForm.usrAvatar]"
      >
      </el-image>
    </el-row>
    <el-row>
      <h2>基本信息</h2>
      <el-divider></el-divider>
    </el-row>
    <el-row>
      <el-col :xs="24" :sm="12" :md="12" :lg="8" :xl="8">
        <h3 class="el-icon-monitor">ID</h3>
        <p>{{ userForm.usrId }}</p>
      </el-col>
      <el-col :xs="24" :sm="12" :md="12" :lg="8" :xl="8">
        <h3 class="el-icon-s-custom">账号</h3>
        <p>{{ userForm.usrAccount }}</p>
      </el-col>
      <el-col :xs="24" :sm="12" :md="12" :lg="8" :xl="8">
        <h3 class="el-icon-phone">电话</h3>
        <p>{{ userForm.usrPhone }}</p>
      </el-col>
      <el-col :xs="24" :sm="12" :md="12" :lg="8" :xl="8">
        <h3 class="el-icon-message">邮箱</h3>
        <p>{{ userForm.usrEmail }}</p>
      </el-col>
      <el-col :xs="24" :sm="12" :md="12" :lg="8" :xl="8">
        <h3 class="el-icon-trophy">身份</h3>
        <p>{{ userForm.usrVipLevel }}</p>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      loading: true,
    };
  },
  computed: {
    ...mapState("user", ["userForm"]),
  },
  methods: {
    ...mapActions("login", { getInfo: "getInfo" }),
  },
  activated() {
    console.log("activated");
    this.getInfo(this);
    setTimeout(() => {
      this.loading = false;
    }, 1000);
  },
  deactivated() {
    console.log("deactivated");
    this.loading = true;
  },
};
</script>

<style scoped>
#selfInfo {
  padding: 30px 25px;
}
h3 {
  box-shadow: inset 5px 0px 0 0 rgb(28, 173, 240);
  padding: 0 10px;
  margin: 10px 0;
}
p {
  margin: 20px 0;
  padding-left: 10px;
  transition: all 0.3s;
}
p:hover {
  background-color: #e9e7e7;
}
</style>
